$button_conlor :#f16c7c;
$button_conlor :#f13c51;
$button_conlor :#AA2A39;
$button_conlor :#e31e24;
$red1 :#E2001A;
$red2 :#E2001A;
$red3 :#c41314;
$button_conlor :#a71011;
$button_conlor :#930011;
$button_conlor :#961011;

*{ 
  font-family: 'Roboto', sans-serif;
}

%red-button{
  margin: 5px 8px;
  text-transform: uppercase;
  display: flex;
  padding: 4px;
  & a{
    @include adaptiv-font(15,10);
    background-color: $red1;
    border-style: solid ;
    border-width: 4px;
    border-radius: 7px; 
    padding: 6px 5px;
    font-weight: 600;
    color: white;
    border-color: $red1;
    transition: 0.25s;
    &:hover{
      color: $red1;
      background-color:white;
    }

  }
}
.container {
  max-width: 1180px; 
  margin: 0px auto;
}
//верх
  header{
    background-color: white;
    z-index: 5;
  }
  .header{
    &__item{
    @extend %red-button;
    justify-content: flex-end;
    }
  }
  .header__call{
    @media (max-width: 480px) {
      display: none;
    }
  }
//панель навигации
  $height__body:60px;
  .header__body{
    box-shadow: 0px 15px 5px -12px rgba(0, 0, 0, .2);
    background-color: white;
    position: relative;
    top: 0;
    height: $height__body;
    width: 100%;
    display: flex;
    &.active{
      position: fixed;
    }
    @media (max-width: 767px) {

    }
  }
  .header__contact-list{
    display: flex;
    flex-direction: row;
    &.scroll{
      padding-bottom: $height__body
    }
  }
  .header__logo {
    align-self: center;
    width: 300px;
    & img{
      object-fit: contain;
    }
  }
  .header__menu{
    display: flex;
    flex-wrap:wrap;
  }
  .header__wrapper{
    max-width: 1180px; 
    margin: 0px auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding-left: 10px;
    @media (max-width: 767px) {
      width:210px;
    }
  }
  .header__list{
    position: relative;
    display: flex;
    padding-top: 5px;
    margin: 10px 10px;
    &>li {
      list-style: none;
      margin: 0 0 0 20px;
      &>a{
        color: $red1;
        font-size: 16px;
        font-weight: 700;
        &:hover{
          color: #fff;
          $wid:1;
          text-shadow: $wid+px 0px 1px $red1,
          -$wid+px 0px 1px $red1,
          0px 1px 1px $red1,
          0px -1px 1px $red1;;
        }
      }
    }
  }
  @media (min-width: 767px){
    .current-page:after{
      margin-top: 6px;
      content: '';
      display: block;
      height: 2px;
      background: $red1;
    }
  } 
//адаптив меню
  $height__adaptive-body:40px;
  @media (max-width: 767px) {
  body.lock {
  overflow: hidden;
  }
  .header__body {height: $height__adaptive-body;}
  .header__contact-list.scroll{padding-bottom: $height__adaptive-body;}
  .header__logo {
  flex: 0 0 200px;
  }
  .header__menu{
    display: block;
    position: absolute;
    top: 0;
    left: -100%;
    top: 0;
    transition: all 0.3s ease 0s;
    display: flex;
    height: 100%;
    width: 100%;
    &.active{
      left: 0%;
    }
  }
  .header__list{
    z-index: 3;
    background-color: white;
    margin: 0;
    flex-direction: column;
    width: 100%;
    justify-content: center;
    &>li{
      align-self: center;
      margin: 10px auto;
      &>a{
        font-size: 20px;
        padding: 10px;

      }
    }
  }
//бургер в адаптиве
  .header__burger {
  display: block;
  width: 33px;
  min-width: 33px;
  height: 29px;
  position: relative;
  z-index: 6;
  margin: 5px;
  }
  .header__burger:before,.header__burger:after{
    content: '';
    background-color: $red1;
    position: absolute;
    width: 100%;
    height: 3px;
    left: 0;
    transition: all 0.3s ease 0s;
  }
  .header__burger:before{
    top: 0;
  }
  .header__burger:after{
    bottom: 0;
  }
  .header__burger>span {
    position: absolute;
    background-color: $red1;
    position: absolute;
    left: 0;
    width: 100%;
    height: 3px;
    top: 13px;
    transition: all 0.3s ease 0s;
  }
  .header__burger.active:before {
    transform: rotate(45deg);
    top: 13px;
    
  }
  .header__burger.active:after {
    transform: rotate(-45deg);
    bottom: 13px;
  }
  .header__burger.active>span {
    transform: scale(0);
  }
  }

//footer
  footer{
    background : url("/assets/images/footer.jpg") center;
  }
  .footer__image{
    box-shadow: 0px -2px 2px 0px rgba(0, 0, 0, .2);
    margin-top: 10px;
  }
  .footer__wrapper{
    display: flex;
    flex-direction: column;
  }
  .footer__contact{
    display: flex;
    margin: 10px;
    justify-content: space-between;
  }
  .footer__policy{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    flex-wrap: wrap;
    margin: 10px;
    &>div{
      display: flex;
      &>span{
      margin: 5px;
      text-align: center;
    }
    }
  }







