.popup{
	z-index: 10;
	position: fixed;
	width: 100%;
	height: 100%;
	background-color: rgba(0,0,0,0.8);
	top: 0;
	left: 0;
	overflow-y: auto;
	overflow-x: hidden;
	opacity: 0;
	visibility: hidden;
	transition: all 0.8s ease 0s;
	&:target{
		opacity: 1;
		visibility: visible;
		& .popup__content{
			transform: translate(0px,0px);
			opacity: 1;
		}
	}
	&__body{
		min-height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 30px 10px;
		transition: all 0.8s ease 0s;
	}
	&__content{
		background-color: white;
		color: black;
		max-width: 800px;
		padding: 30px;
		position: relative;
		transition: all 0.8s ease 0s;
		opacity: 0; 
		transform: translate(0px,-100%);
	}
	&__close{
		position: absolute;
		right: 10px;
		top: 10px;
		font-size: 20px;
		color: black;
		text-decoration: none;
	}
	&__title{
		font-size: 40px;
		margin: 0px 0px 1rem 0px;
	}
	&__area{
		position: absolute;
		height: 100%;
		width: 100%;
		top: 0;
		left: 0;
	}
	&__text{}
}